
import { computed, defineComponent, onMounted, PropType, reactive, ref } from "vue";
import { IonButton, IonPage, IonHeader, IonToolbar, IonContent, IonButtons, IonFooter, IonTitle, IonIcon } from "@ionic/vue";
import { close } from "ionicons/icons";
import { modal } from "@/utils/modal";
import { User } from "@/interfaces/user";
import TextInput from "../inputs/TextInput.vue";
import SelectInput from "../inputs/SelectInput.vue";
import { DTForm } from "../../interfaces/dt_form_field";
import get from "lodash/get";
import { Option } from "@/components/Forms/FieldInterface";
import { UserService } from "@/services/user_service";
import { Role } from "@/interfaces/role";
import { isValidForm, resolveFormValues } from "../../utils/form";
import { isEmpty } from "lodash";
import { toastWarning } from "@/utils/Alerts";
import { RecordConflictError } from "@/services/service";

export default defineComponent({
  name: "UserModal",
  components: {
    IonButton,
    IonPage,
    IonHeader,
    IonContent,
    IonToolbar,
    IonButtons,
    IonFooter,
    IonTitle,
    IonIcon,
    TextInput,
    SelectInput
  },
  props: {
    user: {
      type: Object as PropType<User>,
      default: () => ({}),
    },
  },
  setup(props){
    const roles = ref<Option[]>([]);
    const title = computed(() => {
      return isEmpty(props.user) ? "Add New User" : "Edit User"
    })

    const form = reactive<DTForm>({
      givenName: {
        value: get(props.user, 'person.names[0].given_name', ''),
        label: 'First Name',
        required: true,
        validation: async (name) => `${name.value}`.length > 2 ? null : ['Name must be atleast 3 characters long']
      },
      familyName: {
        validation: async (name) => `${name.value}`.length > 2 ? null : ['Name must be atleast 3 characters long'],
        value: get(props.user, 'person.names[0].family_name', ''),
        label: 'Last Name',
        required: true,
      },
      username: {
        value: get(props.user, 'username', ''),
        label: 'Username',
        required: true,
        disabled: !isEmpty(props.user),
        validation:async (username) => {
          if(`${username.value}`.length < 4){
            return ['Username must be at least 4 characters long']
          }
          return null
        }
      },
      password: {
        value: "",
        label: 'Password',
        required: isEmpty(props.user),
        validation:async ({value}) => {
          if(value && `${value}`.length < 6){
            return ['Password must be at least 6 characters long']
          }
          return null
        }
      },
      confirmPassword: {
        value: "",
        label: 'Confirm Password',
        required: isEmpty(props.user),
        validation: async ({value}, form) => {
          if (value && value !== form.password.value) {
            return ["Passwords not matching"];
          }
          return null;
        }
      },
      roles: {
        value: props.user?.roles?.map((r) => ({ label: r.role, value: r.role, other: r})) || [],
        label: 'Roles',
        placeholder: "Select Roles",
        required: true,
      },
    });

    const submit = async () => {
      if(!(await isValidForm(form))) return
      const { formData } = resolveFormValues(form, true);
      let user = {...formData, roles: formData.roles.map((r: Option) => r.label)}
      try {
        if(isEmpty(props.user)){
          user = await UserService.createUser(user);
        } else {
          user = await UserService.updateUser(props.user['user_id'], user);
        }
        modal.hide(user);
      } catch (error) {
        if(error instanceof RecordConflictError) {
          return form.username.error = "Username already exists"
        }
        toastWarning(JSON.stringify(error), 2000)
      }
    }

    onMounted(() => {
      UserService.getAllRoles().then((userRoles: Role[]) => {
        roles.value = userRoles.map(r => ({
          value: r.role,
          label: r.role,
          other: r,
        }));
      });
    })

    return {
      form,
      close,
      modal,
      roles,
      title,
      submit,
    }
  }
})
